import React from 'react';
import * as S from './styles';
import * as T from './types';

export const Color: React.FC<T.ColorProps> = ({ color }) => {
  console.log(color);
  return (
    <S.Color>
      <div>{color.key}</div>
      <ul>
        {color.variants.map(color => (
          <li>{color}</li>
        ))}
      </ul>
    </S.Color>
  );
};
